header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: var(--bg-color);
}

.toggle-icon {
    cursor: pointer;
    width: 42px;
    height: 42px;
    transition: transform 0.35s ease-in-out;
    float: right;
    margin: 38px 50px 20px 20px;
}

.toggle-icon.rotate {
    transform: rotate(360deg);
}

.toggle-icon:hover {
    scale: 1.1;
}

.logo {
    width: 120px;
    height: 120px;
    margin: 0px 0px 0px 50px;
}

@media screen and (max-width: 600px) {
    .logo {
        margin: 0px 0px 0px 40px;
    }
    .toggle-icon {
        margin: 37px 40px 20px 20px;
    }
}